import Login from "./pages/login"
import { Route } from "react-router"
import { HashRouter } from "react-router-dom"
import Home from "./pages/home";


const App = () => {
  return (
    <HashRouter>
      <Route exact path="/login" component={Login} />
      <Route exact path="/home" component={Home} />
    </HashRouter>
  );
};


export default App