import React from "react"
import { useHistory } from "react-router"
import logo from "../assets/logo.png"
const Login = () => {
    const history = useHistory()
    const onLoginClick = () => {
        history.push("/home")
    }
    return (
        <div className="login-wrapper">
            <div className="col-7 mx-auto pt-200 pb-4">
                <img src={logo} className="img-fluid" alt="Logo" />
            </div>
            <div className="col-11 mx-auto">
                <form>
                    <input
                        type="text"
                        className="login-input-field form-control"
                        placeholder="نام‌کاربری"
                    />
                    <br />
                    <input
                        type="password"
                        className="login-input-field form-control"
                        placeholder="کلمه‌عبور"
                    />
                    <label className="mt-3">
                        <input type="checkbox" className="my-auto align-middle ml-2" />
                        <span className="text-dark my-auto">مرا به خاطر داشته باش</span>
                    </label>
                    <br />
                    <button
                        onClick={onLoginClick}
                        className="btn btn-primary pt-0 w-100 br-20 mt-5 login-btn"
                    >
                        ورود
                    </button>
                </form>
            </div>
        </div>
    )
}

export default Login
