import React from "react"
import logo from "../assets/logo.png"
import jumbo from "../assets/home-jumbotron.jpeg"
import "reactjs-bottom-navigation/dist/index.css"
import BottomNavigation from "reactjs-bottom-navigation"
import { BsFillArrowLeftSquareFill } from "react-icons/bs"
import realestate from "../assets/icons/realestate.png"
import vehicle from "../assets/icons/vehicle.png"
import insurance from "../assets/icons/insurance.png"
import tools from "../assets/icons/tools.png"
import user from "../assets/icons/user.png"

const Home = () => {
    const bottomNavItems = [
        {
            title: "املاک",
            icon: <img src={realestate} style={{ width: "25px", height: "25px" }} alt="Icon" />,
            activeIcon: (
                <img src={realestate} style={{ width: "25px", height: "25px" }} alt="Icon" />
            ),
        },
        {
            title: "خودرو",
            icon: <img src={vehicle} style={{ width: "25px", height: "25px" }} alt="Icon" />,
            activeIcon: <img src={vehicle} style={{ width: "25px", height: "25px" }} alt="Icon" />,
        },
        {
            // title: "پروفایل",
            icon: <img src={user} style={{ width: "50px", height: "50px" }} alt="Icon" />,
            activeIcon: <img src={user} style={{ width: "50px", height: "50px" }} alt="Icon" />,
        },
        {
            title: "تجهیزات",
            icon: <img src={tools} style={{ width: "25px", height: "25px" }} alt="Icon" />,
            activeIcon: <img src={tools} style={{ width: "25px", height: "25px" }} alt="Icon" />,
            // onClick: () => alert("menu clicked"),
        },
        {
            title: "بیمه",
            icon: <img src={insurance} style={{ width: "25px", height: "25px" }} alt="Icon" />,
            activeIcon: (
                <img src={insurance} style={{ width: "25px", height: "25px" }} alt="Icon" />
            ),
        },
    ]
    const createHomeItem = (name, styles) => {
        return (
            <div className={`w-100 home-item ${styles}`} key={name}>
                <br />
                <div className="text-left">{name}</div>
            </div>
        )
    }
    return (
        <div>
            <div className="col-12 row m-0 mx-auto pt-2">
                <div className="col-2"></div>
                <img src={logo} className="img-fluid col-8" alt="Logo" />
                <div className="col-2 my-auto p-0 text-left">
                    <BsFillArrowLeftSquareFill className="text-danger h3" />
                </div>
            </div>
            <div className="col-7 mx-auto font-weight-bold">سامانه اطلاعات دارایی ها</div>
            <div className="col-12 p-0">
                <img src={jumbo} className="img-fluid" alt="Jumbo" />
            </div>
            <div className="home-wrapper">
                <div className="col-10 mx-auto p-0 pt-4 pb-5">
                    <div className="row m-0">
                        <div className="col-6 p-0 font-weight-bold text-secondary text-right">
                            شرکت ها
                        </div>
                        <div className="col-6 p-0 font-weight-bold text-primary text-left">
                            نمایش همه املاک
                        </div>
                    </div>
                    <div className="my-4">
                        <div className="col-12 p-0">
                            {createHomeItem("تاید واتر خاور میانه", "home-item-1")}
                        </div>
                        <div className="row m-0 mt-4">
                            <div className="col-6 pr-0 pl-2">
                                {createHomeItem("بتا", "home-item-2")}
                            </div>
                            <div className="col-6 pl-0 pr-2">
                                {createHomeItem("سهند", "home-item-3")}
                            </div>
                        </div>
                        <div className="row m-0 mt-4">
                            <div className="col-6 pr-0 pl-2">
                                {createHomeItem("امید پارس", "home-item-4")}
                            </div>
                            <div className="col-6 pl-0 pr-2">
                                {createHomeItem("امید ساحل", "home-item-5")}
                            </div>
                        </div>
                        <div className="row m-0 mt-4">
                            <div className="col-6 pr-0 pl-2">
                                {createHomeItem("رایانه گستر", "home-item-6")}
                            </div>
                            <div className="col-6 pl-0 pr-2">
                                {createHomeItem("بیکران قشم", "home-item-7")}
                            </div>
                        </div>
                        <div className="row m-0 mt-4">
                            <div className="col-6 pr-0 pl-2">
                                {createHomeItem("دریا بندر", "home-item-8")}
                            </div>
                            <div className="col-6 pl-0 pr-2">
                                {createHomeItem("تندر ایرانیان", "home-item-9")}
                            </div>
                        </div>
                        <div className="row m-0 mt-4">
                            <div className="col-6 pr-0 pl-2">
                                {createHomeItem("خلیج فارس", "home-item-10")}
                            </div>
                            <div className="col-6 pl-0 pr-2">
                                {createHomeItem("ساحل‌گستر‌پارس", "home-item-11")}
                            </div>
                        </div>
                        <div className="row m-0 mt-4">
                            <div className="col-6 pr-0 pl-2">
                                {createHomeItem("ساحل‌و‌فرا‌ساحل", "home-item-12")}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="mt-5">
                <BottomNavigation
                    items={bottomNavItems}
                    defaultSelected={0}
                    onItemClick={(item) => console.log(item)}
                />
            </div>
        </div>
    )
}

export default Home
